import React from 'react';
import { Row, Col } from 'antd';
import { CloseOutlined } from '@ant-design/icons';
import WalletItem from './WalletItem';
import { WALLETS } from './config';
import './Wallets.scss';
import PropTypes from 'prop-types';

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
const Wallets = (props: any) => {
  return (
    <div className="wallet">
      <CloseOutlined
        className="close"
        onClick={() => {
          props.toggleDisplayWalletModal();
        }}
      />
      <h1>CONNECT WALLET</h1>
      <Row justify="space-around" align="middle" gutter={{ xs: 5, sm: 5, md: 16 }}>
        {Object.values(WALLETS).map((item) => {
          return (
            <Col xs={24} sm={24} md={24} key={item.id}>
              <WalletItem wallet={item} toggleDisplayWalletModal={props.toggleDisplayWalletModal} />
            </Col>
          );
        })}
      </Row>
    </div>
  );
};

Wallets.propTypes = {
  toggleDisplayWalletModal: PropTypes.func.isRequired,
};
export default Wallets;
