import { useEffect, useReducer } from 'react';
import { readContractReducer, FETCH_STATE } from 'src/utils/index';
import { useInvitationContract } from './useContract';
import { useIsMounted } from './useIsMounted';
import { ethers } from 'ethers';
import { useWallet } from './useWallet';
import { useUpdate } from './useUpdate';

const decodeInviterRecord = (record: any) => ({
  who: record.who,
  inviter: record.inviter,
  //padding with 28 zero bytes
  invitationCode: ethers.utils.parseBytes32String(record.invitationCode),
  //first, second, ..... layer of invitee count
  inviteeAmounts: record.inviteeAmounts.map((item: any) => item.toNumber()),
});

export const useDefaultInviterCode = (): any => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const invitationContract = useInvitationContract();
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!invitationContract) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await invitationContract.defaultInviterCode();
        if (isMounted) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: ethers.utils.parseBytes32String(data) });
        }
      } catch (err) {
        console.error(err);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [invitationContract, isMounted, updateByTimer]);

  return state;
};

