export const useRegisteredInvitationCodeContains = (): any => {
  const invitationContract = useInvitationContract();
  const isValidInvitation = async (code: string) => {
    if (!code) {
      return false;
    }
    try {
      const result = await invitationContract.registeredInvitationCodeContains(ethers.utils.formatBytes32String(code));
      return result;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  return { isValidInvitation };
};
