import { ethers } from 'ethers';
import { useEffect, useReducer } from 'react';
import { FETCH_STATE, readContractReducer } from 'src/utils/index';
import { useTokenContract } from './useContract';
import { useIsMounted } from './useIsMounted';
import { useUpdate } from './useUpdate';
import { useWallet } from './useWallet';

export const useTokenBalance = (tokenAddress, accountAddress = null) => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const tokenContract = useTokenContract(tokenAddress);
  const { account, ethersProvider } = useWallet();
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      const utils = ethers.utils;

      try {
        if (!(ethersProvider && tokenContract && (ethers.utils.isAddress(accountAddress) || account))) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        let data;
        if (parseInt(tokenAddress, 16) === 0) {
          data = await ethersProvider.getBalance(accountAddress || account);
        } else {
          data = await tokenContract.balanceOf(accountAddress || account);
        }
        if (isMounted) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: Number(utils.formatEther(data)) });
        }
      } catch (error) {
        console.error(error);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [tokenContract, account, accountAddress, ethersProvider, isMounted, updateByTimer, tokenAddress]);
  return state;
};

export const useTokenAllowance = (tokenAddress, targetContractAddress, accountAddress = null) => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const tokenContract = useTokenContract(tokenAddress);
  const { account, ethersProvider } = useWallet();
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      const utils = ethers.utils;
      try {
        if (
          !(
            targetContractAddress &&
            ethersProvider &&
            tokenContract &&
            (ethers.utils.isAddress(accountAddress) || account)
          )
        ) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await tokenContract.allowance(accountAddress || account, targetContractAddress);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: Number(utils.formatEther(data)) });
        }
      } catch (error) {
        console.error(error);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [tokenContract, account, accountAddress, ethersProvider, isMounted, updateByTimer, targetContractAddress]);
  return state;
};
