import React, { useState } from 'react';
import classnames from 'classnames';

import { Button } from '@/components/UI/Button';

import Style from './Style.module.scss';
import CountdownBanner from '../../components/CountdownBanner/CountdownBanner';
import { formatNumber, registerToken, formatOutput, checkTime } from '../../utils/index';
import { useCalcClaim, useGetConfig, useGetRecord } from 'src/hooks/useIDO';
import { useIDOContract, useTokenContract } from 'src/hooks/useContract';
import { useWallet } from 'src/hooks/useWallet';
import { message } from 'antd';
import { useIsMounted } from 'src/hooks/useIsMounted';
import {
  useDefaultInviterCode,
  useInviterRecords,
  useRegisteredAccountLength,
  useRegisteredInvitationCodeContains,
} from 'src/hooks/useInvitation';
import Countdown from 'src/components/Countdown/Countdown';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useTokenAllowance, useTokenBalance } from 'src/hooks/useToken';
import { ALPHA_ADDRESS, IDO_ADDRESS, TOKENS } from 'src/constants/addresses';
import { ethers } from 'ethers';
import { useQuery } from 'src/hooks/useQuery';
import { END_TIME, START_TIME } from '@/constants/misc';

const PAYMENT_TOKEN = TOKENS.APT;

const PreSale = () => {
  const [loading, setLoading] = useState({
    fundLoading: false,
    claimLoading: false,
    claimBonusLoading: false,
    approveLoading: false,
  });
  const isMounted = useIsMounted();
  const [activeTab, setActiveTab] = useState(0);
  const [fundAmount, setFundAmount] = useState('0');
  const { account } = useWallet();
  const { data: config } = useGetConfig();
  const { data: record } = useGetRecord();
  const { data: claimBalance } = useCalcClaim();
  const { data: inviterRecord } = useInviterRecords();
  const { data: nativeBalance } = useTokenBalance(TOKENS.APT);
  const { data: busdBalance } = useTokenBalance(TOKENS.APT);
  const { data: registeredAccountLength } = useRegisteredAccountLength();
  const { data: defaultInviterCode } = useDefaultInviterCode();
  const { data: paymentTokenAllowance } = useTokenAllowance(PAYMENT_TOKEN, IDO_ADDRESS);
  // console.log(config, record, claimBalance, inviterRecord, nativeBalance, busdBalance);
  const { isValidInvitation } = useRegisteredInvitationCodeContains();
  const queryInviterCode = useQuery()?.get('referral');

  const paymentTokenContract = useTokenContract(PAYMENT_TOKEN);
  const IDOContract = useIDOContract();

  // const totalIDOToken = parseFloat(config?.totalPaymentTokenAmount) * parseFloat(config?.price);
  // const goalIDOToken = parseFloat(config?.paymentTokenCap) * parseFloat(config?.price);
  // const presaleRatio = goalIDOToken !== 0 ? totalIDOToken / goalIDOToken : 0;
  const totalIDOToken = 0;
  const goalIDOToken = 20000000;
  const presaleRatio = 0;

  const referralLink = `${window.location.origin}/#/presale?referral=${inviterRecord?.invitationCode}`;
  const inviteeAmount = inviterRecord?.inviteeAmounts?.length && inviterRecord.inviteeAmounts[0];

  const tabs = [
    {
      label: 'Presale Info',
      value: 0,
    },
    {
      label: 'Claim Token',
      value: 1,
    },
  ];

  const handleFundAmountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (Number.isNaN(Number(value))) {
      return;
    }
    setFundAmount(value);
  };

  const handleDepositMax = () => {
    if (busdBalance) {
      const amount = busdBalance - 0.001 > 0 ? busdBalance - 0.001 : 0;
      setFundAmount(amount.toString());
    }
  };

  const fund = async () => {
    if (loading.fundLoading) {
      return;
    }

    if (!(IDOContract && account && config && defaultInviterCode && inviterRecord)) {
      message.info('Please Connect Wallet');
      return;
    }

    // check time
    const applyStartTime = config.applyStartTime * 1000;
    const applyEndTime = config.applyEndTime * 1000;
    if (!checkTime(applyStartTime)) {
      message.info('Presale has not started');
      return;
    }
    if (checkTime(applyEndTime)) {
      message.info('Presale ended');
      return;
    }

    // test for BUSD balance
    if (Number(fundAmount) > busdBalance) {
      message.info(`Not enough APT`);
      return;
    }

    // test for min max amount
    if (Number(fundAmount) < config.minimalPayAmount) {
      message.info(`The minimum purchase amount is ${config.minimalPayAmount} APT`);
      return;
    }

    // check for gas
    if (nativeBalance < 0.005) {
      message.info('Need gas');
      return;
    }

    let invitationCode = defaultInviterCode;
    if (inviterRecord.invitationCode) {
      invitationCode = '';
    } else if (queryInviterCode) {
      // handle invitationCode from query param
      if (!(await isValidInvitation(queryInviterCode))) {
        message.info('Invalid referral code');
        return;
      }
      invitationCode = queryInviterCode;
    }
    try {
      setLoading((prev) => ({ ...prev, fundLoading: true }));
      const tx = await IDOContract.fund(
        ethers.utils.parseEther(fundAmount),
        ethers.utils.formatBytes32String(invitationCode),
      );
      console.log('tx', tx);
      message.success('Purchase request sent.');
      const receipt = await tx.wait();
      console.log('receipt', receipt);
      message.success('Purchase successfully.');
    } catch (error) {
      console.log(error);
    } finally {
      if (!isMounted) {
        return;
      }
      setLoading((prev) => ({ ...prev, fundLoading: false }));
    }
  };

  const claim = async () => {
    if (loading.claimLoading) {
      return;
    }

    if (!(IDOContract && account && config && record && claimBalance)) {
      message.info('Please Connect Wallet');
      return;
    }

    // check time
    const claimStartTime = config.claimStartTime * 1000;
    if (!checkTime(claimStartTime)) {
      message.info('Claim has not started');
      return;
    }

    // check if claimed
    if (record.claimed) {
      message.info('Already claimed');
      return;
    }

    // check balance
    if (claimBalance.idoTokenAmount === 0 && claimBalance.unusedPaymentTokenAmount === 0) {
      message.info('Nothing to claim');
      return;
    }

    // check for gas
    if (nativeBalance < 0.005) {
      message.info('Need gas');
      return;
    }

    try {
      setLoading((prev) => ({ ...prev, claimLoading: true }));
      const tx = await IDOContract.claim();
      console.log('tx', tx);
      message.success('Claim request sent.');
      const receipt = await tx.wait();
      console.log('receipt', receipt);
      message.success('Claim successfully.');
    } catch (error) {
      console.log(error);
    } finally {
      if (!isMounted) {
        return;
      }
      setLoading((prev) => ({ ...prev, claimLoading: false }));
    }
  };

  const claimBonus = async () => {
    if (loading.claimBonusLoading) {
      return;
    }

    if (!(IDOContract && account && config && record && claimBalance)) {
      message.info('Please Connect Wallet');
      return;
    }

    // check time
    const claimStartTime = config.claimStartTime * 1000;
    if (!checkTime(claimStartTime)) {
      message.info('Bonus claim has not started');
      return;
    }

    // check if claimed
    if (record.bonusClaimed) {
      message.info('Bonus already claimed');
      return;
    }

    // check bonus balance
    if (claimBalance.inviterIdoTokenAmount === 0) {
      message.info('Nothing to claim');
      return;
    }

    // check for gas
    if (nativeBalance < 0.005) {
      message.info('Need gas');
      return;
    }

    try {
      setLoading((prev) => ({ ...prev, claimBonusLoading: true }));
      const tx = await IDOContract.claimbonus();
      console.log('tx', tx);
      message.success('Claim request sent.');
      const receipt = await tx.wait();
      console.log('receipt', receipt);
      message.success('Claim successfully.');
    } catch (error) {
      console.log(error);
    } finally {
      if (!isMounted) {
        return;
      }
      setLoading((prev) => ({ ...prev, claimBonusLoading: false }));
    }
  };

  const approve = async () => {
    if (!(account && paymentTokenContract)) {
      message.info('Please Connect Wallet');
      return;
    }
    // check for gas
    if (nativeBalance < 0.005) {
      message.info('Need gas');
      return;
    }
    try {
      setLoading((prev) => ({ ...prev, approveLoading: true }));
      const tx = await paymentTokenContract.approve(
        IDO_ADDRESS,
        '0xFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFF',
      );
      console.log('tx', tx);
      message.success('Approve request sent.');
      const receipt = await tx.wait();
      console.log('receipt', receipt);
      message.success('Approve successfully.');
    } catch (e) {
      console.log(e);
    } finally {
      if (!isMounted) {
        setLoading((prev) => ({ ...prev, approveLoading: false }));
      }
    }
  };

  const getReferralLink = () => {
    if (!account) {
      return (
        <div className={Style.ReferralLink}>
          <div className={Style.ReferralText}>Please connect wallet to get referral link</div>
        </div>
      );
    }
    if (!inviterRecord?.invitationCode) {
      return (
        <div className={Style.ReferralLink}>
          <div className={Style.ReferralText}>Please participate in the presale to activate your referral link</div>
        </div>
      );
    }
    return (
      <div className={Style.ReferralLink}>
        <div className={Style.ReferralText}>Referral Link: {referralLink}</div>
        <CopyToClipboard
          text={referralLink}
          onCopy={() => {
            message.success('Referral Link Copied!');
          }}
        >
          <img className={Style.CopyButton} src="/static/images/copy.svg" alt="copy" />
        </CopyToClipboard>
      </div>
    );
  };
  return (
    <div className={Style.PreSale}>
      <CountdownBanner />
      <div className={Style.Card}>
        <div className={Style.CardItem}>
          <div className={Style.CardItemHeader}>
            <img src="/static/images/preSale/logo.png" />
            <div className={Style.CardTitle}>
              AlphaSwap <br /> (ALPHA)
            </div>
          </div>

          <div className={Style.ClubCopies}>
            <span>
              {formatNumber(totalIDOToken)}/{' '}
              <span className={Style.ClubCopiesTotal}>{formatNumber(goalIDOToken)} ALPHA</span>
            </span>
            <span>{formatNumber(presaleRatio, '0.[00]%')}</span>
          </div>
          <div className={Style.ClubProgress}>
            <div className={Style.ClubProgressValue} style={{ width: `${presaleRatio * 100}%` }} />
          </div>

          <div className={Style.ClubTotal}>
            <span>Presale Goal:</span>
            <span className={Style.ClubTotalValue}>
              {formatNumber(goalIDOToken)} ALPHA <br />
              <span className={Style.ClubTotalBnb}>= 10,000 {/*formatNumber(config?.paymentTokenCap)*/} APT</span>
            </span>
          </div>
        </div>

        <div className={Style.CardItem}>
          <div className={Style.CardItemHeader}>
            <div className={Style.CardTitle}>DEPOSIT TO PARTICIPATE</div>
          </div>
          <div className={Style.Amount}>
            <div className={Style.AmountTitleWrap}>
              <div className={Style.AmountTitle}>Amount:</div>
              <div className={Style.AmountMax} onClick={handleDepositMax}>
                MAX
              </div>
            </div>
            <div className={Style.AmountInputWrap}>
              <input value={fundAmount} onChange={handleFundAmountChange} type="text" />
              <div className={Style.UnitWrap}>
                <img src="/static/images/preSale/apt.png" alt="apt" /> APT
              </div>
            </div>
          </div>

          <div className={Style.Field}>
            <div>APT Balance:</div>
            <div>{formatNumber(busdBalance)}</div>
          </div>

          <div className={Style.Field}>
            <div>APT invested:</div>
            <div>{formatNumber(record?.paymentTokenAmount)}</div>
          </div>

          <div className={Style.Button}>
            {paymentTokenAllowance > 0 ? (
              <Button onClick={fund} loading={loading.fundLoading}>
                Purchase
              </Button>
            ) : (
              <Button onClick={approve} loading={loading.approveLoading}>
                Approve
              </Button>
            )}
          </div>
        </div>
      </div>

      <div className={Style.Tab}>
        {tabs.map((item) => (
          <div
            className={classnames(Style.TabItem, { [Style.TabItemActive]: activeTab === item.value })}
            key={item.value}
            onClick={() => setActiveTab(item.value)}
          >
            {item.label}
          </div>
        ))}
      </div>

      {!activeTab ? (
        <IAOInfo config={config} />
      ) : (
        <TokenInformation
          claim={claim}
          config={config}
          record={record}
          claimBalance={claimBalance}
          registeredAccountLength={registeredAccountLength}
          loading={loading}
        />
      )}

      <div className={Style.InviteWrap}>
        <h2>INVITE YOUR FRIENDS TO EARN 10% ALPHA BONUS ON PRESALE.</h2>
        <p>
          Every direct referral provides 10% bonus ALPHA tokens during the presale period. Referral rewards can be
          claimed after the presale ends. You must deposit in the presale first to active your link.
        </p>
        {getReferralLink()}
        <div className={Style.ReferralStatsWrap}>
          <div className={Style.ReferralStats}>
            <div className={Style.ReferralStatsItem}>
              <h2>Total Referrals</h2>
              <p>{formatNumber(inviteeAmount)}</p>
            </div>
            {/* <div className={Style.ReferralStatsItem}>
              <h2>Reward Claimed</h2>
              <p>- ALCO</p>
            </div> */}
            <div className={Style.ReferralStatsItem}>
              <h2>Available Reward</h2>
              <p>{formatNumber(claimBalance?.inviterIdoTokenAmount)} ALPHA</p>
            </div>
          </div>

          <Button className={Style.ClaimButton} onClick={claimBonus} loading={loading.claimBonusLoading}>
            {record?.bonusClaimed ? 'Claimed' : 'Claim'}
          </Button>
        </div>
      </div>
    </div>
  );
};

const TokenInformation = (props: {
  claim: React.MouseEventHandler<HTMLDivElement>;
  config: any;
  record: any;
  claimBalance: any;
  registeredAccountLength: number;
  loading: any;
}) => {
  const { claim, config, record, claimBalance, loading } = props;
  const proportion = formatNumber(
    record && config && (record.paymentTokenAmount / config.totalPaymentTokenAmount) * 100,
  );
  const claimStartTime = config?.claimStartTime * 1000;
  const list1 = [
    {
      label: 'Participate Amount:',
      value: `${formatNumber(record?.paymentTokenAmount)} APT`,
    },
    {
      label: 'Your Proportion:',
      value: `${proportion}%`,
    },
    {
      label: 'Refund Amount:',
      value: `${formatNumber(claimBalance?.unusedPaymentTokenAmount)} APT`,
    },
    {
      label: 'Token Purchased:',
      value: `${formatNumber(claimBalance?.idoTokenAmount)} ALPHA`,
    },
    {
      label: 'Claim Countdown:',
      value: checkTime(claimStartTime) ? 'Claim Started' : <Countdown timestamp={claimStartTime} />,
    },
  ];

  return (
    <div className={Style.TabContent}>
      <div className={Style.TabContentItem}>
        <div className={Style.TabTitle}>Presale information</div>
        {list1.map((item) => {
          return (
            <div className={Style.TabField} key={item.label}>
              <div>{item.label}</div>
              <div className={Style.TabFieldValue}>{item.value}</div>
            </div>
          );
        })}
      </div>

      <div className={Style.TabContentItem}>
        <div className={Style.TabContentItemButton}>
          <Button onClick={claim} loading={loading.claimLoading}>
            {record?.claimed ? 'Claimed' : 'Claim ALPHA'}
          </Button>
        </div>
      </div>
    </div>
  );
};

const IAOInfo = (props: { config: any }) => {
  const { config } = props;
  // const startTime = formatOutput(config?.applyStartTime && new Date(config?.applyStartTime * 1000).toUTCString());
  // const endTime = formatOutput(config?.applyEndTime && new Date(config?.applyEndTime * 1000).toUTCString());
  // const price = formatOutput(config?.price && 1 / parseFloat(config?.price));
  // const minimalPayAmount = formatOutput(config?.minimalPayAmount && parseFloat(config?.minimalPayAmount));

  const startTime = formatOutput(new Date(START_TIME * 1000).toUTCString());
  const endTime = formatOutput(new Date(END_TIME * 1000).toUTCString());
  const price = formatOutput(0.0005);
  const minimalPayAmount = formatOutput(1);

  const list1 = [
    {
      label: 'Start Time:',
      value: startTime.substr(startTime.indexOf(' ') + 1),
      // value: 'Jul 25 2022 15:00 GMT',
    },
    {
      label: 'End Time:',
      value: endTime.substr(endTime.indexOf(' ') + 1),
      // value: 'Aug 8 2022 15:00 GMT',
    },
    {
      label: 'Presale Price:',
      // value: '0.05 BUSD / ALCO',
      value: `${price} APT / ALPHA`,
    },
    // {
    //   label: 'Available Quota:',
    //   value: '1,000,000 BUSD',
    // },
    {
      label: 'Minimum Investment:',
      value: `${minimalPayAmount} APT`,
    },
    {
      label: 'Presale Method:',
      value: 'Over-subscription',
    },
  ];

  const list2 = [
    {
      label: 'Token full name:',
      value: 'AlphaSwap',
    },
    {
      label: 'Token symbol:',
      value: 'ALPHA',
    },
    {
      label: 'Decimals:',
      value: '18',
    },
    {
      label: 'Total supply:',
      value: '100,000,000',
    },
    {
      label: 'Token Address:',
      value: ALPHA_ADDRESS,
    },
  ];

  return (
    <div className={Style.TabContent}>
      <div className={Style.TabContentItem}>
        <div className={Style.TabTitle}>Presale information</div>
        {list1.map((item) => {
          return (
            <div className={Style.TabField} key={item.label}>
              <div>{item.label}</div>
              <div className={Style.TabFieldValue}>{item.value}</div>
            </div>
          );
        })}
      </div>

      <div className={Style.TabContentItem}>
        <div className={Style.TabTitle}>Token information</div>
        {list2.map((item) => {
          return (
            <div className={Style.TabField} key={item.label}>
              <div>{item.label}</div>
              <div className={Style.TabFieldValue}>{item.value}</div>
            </div>
          );
        })}

        <div
          className={Style.TabContentItemAdd}
          onClick={() => {
            registerToken('', 'ALPHA', 18, null);
          }}
        >
          Add to metamask
        </div>
      </div>
    </div>
  );
};

export { PreSale };
