import { useIsMobileView } from '@/hooks/useIsMobileView';
import classNames from 'classnames';
import React, { useEffect, useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import WalletButton from '../UI/WalletButton/WalletButton';
import Style from './Style.module.scss';

const Menu = ({
  isMobileView,
  toggleMenu,
}: {
  isMobileView: boolean;
  toggleMenu: React.MouseEventHandler<HTMLAnchorElement>;
}) => {
  const menu = [
    {
      label: 'SWAP',
      path: '/swap',
    },
    {
      label: 'TAP',
      path: '/tap',
    },
    // {
    //   label: 'BREWERY',
    //   path: '/brewery',
    // },
    {
      label: '',
      render() {
        return <img src="/static/images/index/menuCenter.svg" alt="aico0l" />;
      },
      path: '/',
    },
    {
      label: 'IDO',
      path: '/ido',
    },

    {
      label: 'Docs',
      path: 'https://alpha-16.gitbook.io/',
      external: true,
    },
  ];

  return (
    <div className={Style.Menu}>
      {menu.map((item) => {
        if (item.render) {
          if (isMobileView) {
            return null;
          }
          return (
            <Link to={item.path} className={Style.MenuLogo} key={item.path}>
              {item.render()}
            </Link>
          );
        }
        if (item.external) {
          return (
            <a className={Style.MenuItem} href={item.path} target="_blank" rel="noopener noreferrer" key={item.path}>
              <img src="/static/images/header/itemBg.png" alt="item" />
              <p>{item.label}</p>
            </a>
          );
        }

        return (
          <NavLink
            to={item.path}
            className={({ isActive }: { isActive: boolean }) =>
              classNames(Style.MenuItem, { [Style.active]: isActive })
            }
            key={item.path}
            onClick={toggleMenu}
          >
            <img src="/static/images/header/itemBg.png" alt="item" />
            <p>{item.label}</p>
          </NavLink>
        );
      })}
    </div>
  );
};

Menu.defaultProps = {
  isMobileView: false,
};

const Header = (): JSX.Element => {
  const isMobileView = useIsMobileView();
  const [menuCollapsed, setMenuCollapsed] = useState(isMobileView || false);

  useEffect(() => {
    if (isMobileView) {
      setMenuCollapsed(true);
    } else {
      setMenuCollapsed(false);
    }
  }, [isMobileView]);

  const toggleMenu = () => {
    if (isMobileView) {
      setMenuCollapsed(!menuCollapsed);
    }
  };

  return (
    <div className={Style.Header}>
      {!isMobileView && <WalletButton className={Style.Hide} />}
      <div className={classNames(Style.NavBar, Style.LayoutContent)}>
        {isMobileView && (
          <div className={Style.mobileHeader}>
            <img
              className={Style.mobileMenuIcon}
              src="/static/images/header/menu.svg"
              alt="menu"
              onClick={toggleMenu}
            />
            <img className={Style.mobileMenuLogo} src="/static/images/index/menuCenter.svg" alt="aico0l" />
            <WalletButton />
          </div>
        )}
        {!menuCollapsed && <Menu isMobileView={isMobileView} toggleMenu={toggleMenu} />}
      </div>
      {!isMobileView && <WalletButton className={classNames(Style.SideButton)} />}
    </div>
  );
};

export { Header };
