import { useMemo } from 'react';
import { ethers, Contract } from 'ethers';
import { useWallet } from './useWallet';
import IDO_ABI from '../abis/IDO.json';
import MULTICALL_ABI from '../abis/MULTICALL.json';
import INVITATION_ABI from '../abis/INVITATION.json';
import ALCOOL_ABI from '../abis/ALCOOL.json';
import { IDO_ADDRESS, MULTICALL_ADDRESS, INVITATION_ADDRESS } from '../constants/addresses';

export const useIDOContract = (): any => {
  const { ethersProvider, account } = useWallet();
  return useMemo(() => {
    if (!ethersProvider) {
      return null;
    }
    return account
      ? new Contract(IDO_ADDRESS, IDO_ABI.abi, ethersProvider.getSigner())
      : new Contract(IDO_ADDRESS, IDO_ABI.abi, ethersProvider);
  }, [account, ethersProvider]);
};

export const useMulticallContract = (): any => {
  const { ethersProvider } = useWallet();
  return useMemo(() => {
    if (!ethersProvider) {
      return null;
    }
    return new Contract(MULTICALL_ADDRESS, MULTICALL_ABI.abi, ethersProvider);
  }, [ethersProvider]);
};

export const useInvitationContract = (): any => {
  const { ethersProvider, account } = useWallet();
  return useMemo(() => {
    if (!ethersProvider) {
      return null;
    }
    return account
      ? new Contract(INVITATION_ADDRESS, INVITATION_ABI.abi, ethersProvider.getSigner())
      : new Contract(INVITATION_ADDRESS, INVITATION_ABI.abi, ethersProvider);
  }, [account, ethersProvider]);
};

export const useTokenContract = (address: string): any => {
  const { ethersProvider, account } = useWallet();
  return useMemo(() => {
    if (!(ethersProvider && ethers.utils.isAddress(address))) {
      return null;
    }
    return account
      ? new Contract(address, ALCOOL_ABI.abi, ethersProvider.getSigner())
      : new Contract(address, ALCOOL_ABI.abi, ethersProvider);
  }, [ethersProvider, address, account]);
};
