import React, { useState } from 'react';
import classNames from 'classnames';
import { Modal } from 'antd';
import { useWallet } from '@/hooks/useWallet';
import Wallets from '@/components/Wallet/Wallets';
import Style from './WalletButton.module.scss';
import { truncAddress } from '@/utils';

const WalletButton = ({ className }: { className: string }): JSX.Element => {
  const [showWalletModal, setShowWalletModal] = useState<boolean>(false);

  const { account, disconnect } = useWallet();

  const toggleDisplayWalletModal = () => {
    setShowWalletModal(!showWalletModal);
  };

  return (
    <div
      className={classNames(Style.WalletButton, className)}
      onClick={() => {
        {
          account ? disconnect() : toggleDisplayWalletModal();
        }
      }}
    >
      <img src="/static/images/header/itemBg.png" alt="item" />
      <p>{account ? truncAddress(account) : 'Connect'}</p>

      <Modal
        visible={showWalletModal}
        onCancel={toggleDisplayWalletModal}
        footer={null}
        centered={true}
        wrapClassName={'customModal'}
      >
        <Wallets toggleDisplayWalletModal={toggleDisplayWalletModal} />
      </Modal>
    </div>
  );
};

WalletButton.defaultProps = { className: '' };

export default WalletButton;
