import { useEffect, useReducer } from 'react';
import { readContractReducer, FETCH_STATE } from 'src/utils/index';
import { useIDOContract } from './useContract';
import { useIsMounted } from './useIsMounted';
import { ethers } from 'ethers';
import { useWallet } from './useWallet';
import { useUpdate } from './useUpdate';

const decodeConfig = (config: any) => {
  const utils = ethers.utils;
  return {
    paymentToken: config._paymentToken,

    price: config._price.toNumber(),

    minimalPayAmount: Number(utils.formatEther(config._minimalPayAmount)),

    paymentTokenCap: Number(utils.formatEther(config._paymentTokenCap)),

    applyStartTime: config._applyStartTime.toNumber(),
    applyEndTime: config._applyEndTime.toNumber(),
    // applyEndTime: 1661405717, // TODO: remove this placeholder end time
    claimStartTime: config._claimStartTime.toNumber(),

    inviterBonusPercent: config._inviterBonusPercent.toNumber(),

    totalPaymentTokenAmount: Number(utils.formatEther(config._totalPaymentTokenAmount)),
  };
};

const decodeRecord = (record: any) => {
  const utils = ethers.utils;
  return {
    claimed: record.claimed,
    bonusClaimed: record.bonusClaimed,
    paymentTokenAmount: Number(utils.formatEther(record.paymentTokenAmount)),
    inviter: record.inviter,
    totalInviteePaymentTokenAmount: Number(utils.formatEther(record.totalInviteePaymentTokenAmount)),
  };
};

const decodeCalcClaim = (record: any) => {
  const utils = ethers.utils;
  return {
    idoTokenAmount: Number(utils.formatEther(record.idoTokenAmount)),
    unusedPaymentTokenAmount: Number(utils.formatEther(record.unusedPaymentTokenAmount)),
    inviterIdoTokenAmount: Number(utils.formatEther(record.inviterIdoTokenAmount)),
  };
};

export const useGetConfig = (): any => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const IDOContract = useIDOContract();
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!IDOContract) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await IDOContract.getConfig();
        if (isMounted) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: decodeConfig(data) });
        }
      } catch (err) {
        console.error(err);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [IDOContract, isMounted, updateByTimer]);

  return state;
};

export const useGetRecord = (address?: string): any => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const IDOContract = useIDOContract();
  const isMounted = useIsMounted();
  // for account address
  const { account } = useWallet();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!(IDOContract && ((address && ethers.utils.isAddress(address)) || account))) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await IDOContract.getRecord(address || account);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: decodeRecord(data) });
        }
      } catch (err) {
        console.error(err);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [IDOContract, isMounted, account, address, updateByTimer]);
  return state;
};

export const useCalcClaim = (address?: string): any => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const IDOContract = useIDOContract();
  const isMounted = useIsMounted();
  // for account address
  const { account } = useWallet();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!(IDOContract && ((address && ethers.utils.isAddress(address)) || account))) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await IDOContract.calcClaim(address || account);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: decodeCalcClaim(data) });
        }
      } catch (err) {
        console.error(err);
        if (isMounted) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [IDOContract, isMounted, account, address, updateByTimer]);
  return state;
};
