import numeral from 'numeral';

export const MOBILE_BREAK_POINT = 765;

export const SECOND = 1;
export const MINUTES = SECOND * 60;
export const HOUR = MINUTES * 60;
export const DAY = HOUR * 24;
export const MONTH = DAY * 30;
export const YEAR = DAY * 365;

export const truncAddress = (addr: string) => {
  if (addr && addr.length === 42) {
    return `0x...${addr.substring(addr.length - 4)}`;
  }
  return '';
};

// check if a timestamp is from the past, need to be in millisecond
export const checkTime = (ts: number) => new Date().getTime() > ts;

// convert decimal number to Hex string
export const toHex = (decimalNum: number) => `0x${decimalNum.toString(16)}`;

export const registerToken = async (
  tokenAddress: string,
  tokenSymbol: string,
  tokenDecimals: number,
  tokenImage: null,
) => {
  const tokenAdded = await window.ethereum.request({
    method: 'wallet_watchAsset',
    params: {
      type: 'ERC20',
      options: {
        address: tokenAddress,
        symbol: tokenSymbol,
        decimals: tokenDecimals,
        image: tokenImage,
      },
    },
  });

  return tokenAdded;
};

export const FETCH_STATE = {
  INIT: 0,
  SUCCESS: 1,
  FAILURE: 2,
};

// reducer for general contract calls including states: loading, error, data
export const readContractReducer = (state: any, action: any) => {
  switch (action.type) {
    case FETCH_STATE.INIT:
      return {
        ...state,
        isLoading: true,
        isError: false,
      };
    case FETCH_STATE.SUCCESS:
      return {
        ...state,
        isLoading: false,
        isError: false,
        data: action.payload,
      };
    case FETCH_STATE.FAILURE:
      return {
        ...state,
        isLoading: false,
        isError: true,
      };
    default:
      throw new Error('Invalid general contract action');
  }
};

export const formatNumber = (num: any, formatString = '0,0.[00]') => {
  if (!num || parseFloat(num) === 0) {
    return '-';
  }
  return numeral(num).format(formatString);
};

export const formatOutput = (item: any) => {
  return item || '-';
};
