import { LoadingOutlined } from '@ant-design/icons';
import classNames from 'classnames';
import React from 'react';
import Style from './Style.module.scss';

const Button: React.FC<any> = (props: {
  className?: string;
  onClick?: React.MouseEventHandler<HTMLDivElement>;
  children?: React.ReactNode;
  loading?: boolean;
  secondary?: boolean;
  subtext?: string;
}) => {
  const { className, children, onClick, loading, secondary, subtext } = props;
  return (
    <div
      className={classNames(Style.Button, className, { [Style.Loading]: loading }, { [Style.secondary]: secondary })}
      onClick={onClick}
    >
      <div className={Style.Text}>
        <div className={Style.MainText}>{children}</div>
        <div className={Style.Subtext}>{subtext}</div>
      </div>
      {loading ? (
        <>
          &nbsp; <LoadingOutlined />
        </>
      ) : null}
    </div>
  );
};
Button.defaultProps = { className: '', children: null, onClick: undefined, loading: false };
export { Button };
