import { START_TIME } from '@/constants/misc';
import React from 'react';
import { Link } from 'react-router-dom';
import { useGetConfig } from 'src/hooks/useIDO';
import { checkTime } from 'src/utils/index';
import Countdown from '../Countdown/Countdown';
import { Button } from '../UI/Button';

import Style from './CountdownBanner.module.scss';

// const START_TIME = 1658761200 * 1000;

const CountdownBanner = (props: { isHomepage: boolean }): JSX.Element => {
  const { isHomepage } = props;
  const { data: config } = useGetConfig();
  // const applyStartTime = config?.applyStartTime * 1000;
  // const applyEndTime = config?.applyEndTime * 1000;
  const applyStartTime = START_TIME * 1000;
  const applyEndTime = START_TIME * 1000;
  const getCountdown = () => {
    if (!(applyStartTime && applyEndTime)) {
      return;
    }
    if (!checkTime(applyStartTime)) {
      return (
        <div className={Style.countdownWrap}>
          Presale Start in: <Countdown className={Style.countdown} timestamp={applyStartTime} />
        </div>
      );
    }
    if (!checkTime(applyEndTime)) {
      return (
        <div className={Style.countdownWrap}>
          Presale End in: <Countdown className={Style.countdown} timestamp={applyEndTime} />
        </div>
      );
    }
    return null;
  };
  return (
    <div className={Style.CountdownBanner}>
      {/* <img className={Style.bg} src="/static/images/preSale/banner.png" alt="bg" /> */}

      <img className={Style.logo} src="/static/images/preSale/logo.png" alt="logo" />
      {getCountdown()}
      {isHomepage && !checkTime(applyEndTime) && (
        <Link to="presale" className={Style.participateButton}>
          <Button>Join Presale</Button>
        </Link>
      )}
    </div>
  );
};

CountdownBanner.defaultProps = {
  isHomepage: false,
};

export default CountdownBanner;
